import "./PcPost.css";
import React, { useEffect, useState } from "react";
import axios from 'axios';

const myToken = sessionStorage.getItem('reborn-ec-token');
function PcPost() {
    const [deliveryInfo, setDeliveryInfo] = useState({
        name: "",
        phone: "",
        postCode: "",
        prefecture: "",
        city: "",
        street: "",
        buildingName: "",
    });
    const [isEditable, setIsEditable] = useState(false);
    const [error, setError] = useState(null);

    useEffect(() => {
        axios.get('https://www.ict-reborn.link/api/user/delivery', {
            headers: {
                'Authorization': `Bearer ${myToken}`
            }
        })
            .then(response => {
                setDeliveryInfo(response.data.data);
            })
            .catch(err => {
                setError(err);
                console.log(error);
            });
    }, []);

    const handleInputChange = (event, field) => {
        const value = event.target.value;
        setDeliveryInfo(prevState => ({ ...prevState, [field]: value }));
    };

    // AddressUpdate
    const updateDeliveryInfo = () => {
        axios.post('https://www.ict-reborn.link/api/user/delivery/modify', deliveryInfo, {
            headers: {
                'Authorization': `Bearer ${myToken}`
            }
        })
            .then(response => {
                alert("");
                console.log(response);

            })
            .catch(err => {
                setError(err);
            });
    }




    return (
        <>
            <div className="PcPostNav">
                <div className="PcPostNavText">HOME / MYPAGE / </div>

            </div>
            <div className="All">
                
            </div>
            <div className="PcPost-container">
                <div className="PcPost-container2">
                    <div className="PcAQ-container">
                        <div className="PcAQInside">
                            <div className="PcAQInside2">

                                <p className="PcPost-Text"></p>

                                <input
                                    className="custom-input"
                                    value={deliveryInfo && deliveryInfo.name ? deliveryInfo.name : null}
                                    readOnly={!isEditable}
                                    onChange={(e) => handleInputChange(e, "name")}
                                    placeholder=""
                                    onFocus={(e) => e.target.placeholder = ''}
                                    onBlur={(e) => e.target.placeholder = ""}
                                />

                                <p className="PcPost-Text"> </p>
                                <input
                                    className="custom-input"
                                    value={deliveryInfo && deliveryInfo.phone ? deliveryInfo.phone : null}
                                    readOnly={!isEditable}
                                    onChange={(e) => handleInputChange(e, "phone")}
                                    placeholder="-"
                                    onFocus={(e) => e.target.placeholder = ''}
                                    onBlur={(e) => e.target.placeholder = "-"}
                                />
                                <p className="PcPost-Text"> </p>
                                <input
                                    className="custom-input"
                                    value={deliveryInfo && deliveryInfo.postCode ? deliveryInfo.postCode : null}
                                    readOnly={!isEditable}
                                    onChange={(e) => handleInputChange(e, "postCode")}
                                    placeholder="-"
                                    onFocus={(e) => e.target.placeholder = ''}
                                    onBlur={(e) => e.target.placeholder = "-"}
                                />



                                <p className="PcPost-Text"></p>

                                <select
                                    id="prefecture"
                                    className="custom-select"
                                    value={deliveryInfo && deliveryInfo.prefecture}
                                    readOnly={!isEditable}
                                    onChange={(e) => handleInputChange(e, "prefecture")}
                                    defaultValue=""
                                >
                                    <option disabled></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                    <option value=""></option>
                                </select>

                                <p className="PcPost-Text"></p>
                                <input
                                    className="custom-input"
                                    value={deliveryInfo && deliveryInfo.city ? deliveryInfo.city : null}
                                    readOnly={!isEditable}
                                    onChange={(e) => handleInputChange(e, "city")}
                                    placeholder=""
                                    onFocus={(e) => e.target.placeholder = ''}
                                    onBlur={(e) => e.target.placeholder = ""}
                                />

                                <p className="PcPost-Text"></p>
                                <input
                                    className="custom-input"
                                    value={deliveryInfo && deliveryInfo.street ? deliveryInfo.street : null}
                                    readOnly={!isEditable}
                                    onChange={(e) => handleInputChange(e, "street")}
                                    placeholder="1-35"
                                    onFocus={(e) => e.target.placeholder = ''}
                                    onBlur={(e) => e.target.placeholder = "1-35"}
                                />

                                <p className="PcPost-Text"></p>
                                <input
                                    className="custom-input"
                                    value={deliveryInfo && deliveryInfo.buildingName ? deliveryInfo.buildingName : null}
                                    readOnly={!isEditable}
                                    onChange={(e) => handleInputChange(e, "buildingName")}
                                    placeholder=""
                                    onFocus={(e) => e.target.placeholder = ''}
                                    onBlur={(e) => e.target.placeholder = ""}
                                />

                                {/*<p style={{color:"#A4A4A4",fontSize:"14px"}}>48</p>*/}
                                <div className="PcAQInside3">
                                    <button className="PcAQ-btn-L" onClick={() => setIsEditable(true)} ></button>
                                    <button className="PcAQ-btn-R" onClick={() => {
                                        setIsEditable(false);
                                        updateDeliveryInfo();
                                    }} ></button>
                                </div>
                            </div>

                        </div>
                    </div>

                </div>
            </div>

            <div style={{
                background: "white",
                height: "300px",
                width: "100%"
            }}>

            </div>
        </>
    )
}


export default PcPost;